import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import WJH from './js/wjhJS.js'
import animated from 'animate.css'
require('./js/setSize')
import Tool from './js/tool.js'
import Share from './js/shareAndGetName'
const configData = require('./assets/versions.json')

declare module 'vue/types/vue' { // 模块补充 可以去这个路径查阅
    interface Vue {
        url: string,
        wjh: any,
        [key: string]: any // 解决对象的遍历
    }
}
const URL = process.env.NODE_ENV === 'production' && process.env.VUE_APP_TITLE !== 'experiment' ? 'https://api.psy-1.com' : 'https://api.debug.psy-1.com'

Vue.config.productionTip = false
Vue.prototype.$wjh = WJH
Vue.use(animated)

new Vue({
  data () {
      return {
          url: URL,
          imgSaver: '',
          share: null,
          configData: configData[process.env.VUE_APP_TITLE === 'catch' ? 0 : 1]
      }
  },
  methods: {
      shareM (title = '小睡眠官方测评', desc = '小睡眠', picUrl = '') {
          // picUrl ? true : picUrl = this.amountData.weixinPic
          const share = new Share({ pic: picUrl, url: window.location.href.split('#')[0], title, desc })
          this.share = share
          // share.appShare()
          // share.rawWeiXinShare(this.url)
      },
  },
  router,
  store,
  render: (h) => h(App),
    mounted () {
        document.title = this.configData.title
        // @ts-ignore
        _hmt.push(['_trackEvent', 'type', 'click', this.configData.type])
        if (Tool.is_cosleep()) {
            // @ts-ignore
            this.shareM()
            // @ts-ignore
            this.share.appGetName()
        }
        document.documentElement.addEventListener('touchstart', (event) => {
            if (event.touches.length > 1) {
                event.preventDefault()
            }
        }, false)
    }
}).$mount('#app')
