




















import { Component, Vue } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src

@Component({
  components: {
    HelloWorld,
  },
})
export default class Home extends Vue {
    private land: boolean = false
    private landingButton: string = ''
    private changeTopMargin: boolean = false
    private toPlayPage () {
        if (this.$wjh.parseQuery(window.location.href).d) {
            // window.location = 'cosleep://deeplink?code=1053&activity_link=https://www.baidu.com'
            window.location.href = 'https://www.heartide.com/statics/redirect?url=630'
            return
        }
        this.$router.push('./playing')
    }
    private mounted () {
        if (this.$wjh.parseQuery(window.location.href).d) {
            this.land = true
        }
        // console.log(window.innerHeight / window.innerWidth)
        if ((window.innerHeight / window.innerWidth) > 1.7) {
            this.changeTopMargin = true
        }
    }
}
