      },
      {
          randomName: ['回到过去悔恨鬼', '报复性熬夜修仙', '夜深犯饿吃点啥', '明天要早起不要睡过头', '有人呼噜声太响', '孩子操碎心']
      },
      {
          randomName: ['百万负翁鬼', '花呗/白条/信用卡未还', '又到还款日', '贫穷令人为难', '房贷还不清', '缺钱']
      },
      {
          randomName: ['幻想自己能暴富鬼', '暴富是唯一催眠剂', '钱多花不完', '研究致富', '收入太低', '开支太大']
      },
  ]

  private ghosts: ghost[] = []
  private backGhosts: ghost[] = []
  private randomNameMode: boolean = false
  private selectGhosts: ghost[] = []

  private addBackGhosts (): void {
      for (let i = 1; i < this.count + 1; i++) {
          const iString = (i + '').padStart(2, '0')
          this.backGhosts.push({
              detail: {
                  randomName: []
              },
              name: '', // 当前名称
              index: i,
              select: false,
              img: require(`../assets/GHOSTS/img_ghost_${iString}@3x.png`),
              bg: require(`../assets/GHOSTS/bg_ghost_${iString}@3x.png`),
          } as ghost)
      }
  }

  // ghost 处理 的代码
  private ghostsDetailChange (i, randomMode = false) {
      const ghost = this.backGhosts[i]
      ghost.detail = this.ghostsDetail[i]
      randomMode ? ghost.name = ghost.detail.randomName[this.$wjh.randomNum(0, ghost.detail.randomName.length - 1)] : ghost.name = ghost.detail.randomName[this.$root.configData.type === 'reason' ? 1 : 0] // 默认为第二个文案
      this.ghosts.push(this.backGhosts[i])
  }

  private change () {
      // @ts-ignore
      _hmt.push(['_trackEvent', 'nav', 'click', '换一批'])
      this.ghosts = []
      this.$nextTick(() => {
          this.page++
          const allPage: number = Math.floor(this.count / 6) // 全部页数
          const last = this.count % 6 // 剩下的单个个数
          if ((this.page === allPage + 1 && last === 0)) {
              this.page = 1
              this.randomNameMode = true
          }
          if (this.page <= allPage) {
              for (let i = (this.page - 1) * 6; i < (this.page * 6); i++) {
                  this.ghostsDetailChange(i, this.randomNameMode)
              }
              return
          }
          if (this.page === allPage + 1) {
              for (let i = (this.page - 1) * 6; i < ((this.page - 1) * 6) + last; i++) {
                  this.ghostsDetailChange(i, this.randomNameMode)
              }
              this.page = 0
          }
      })
  }

  @Emit('parentChange')
  private selectEvent (data: ghost, event: any) {
      // @ts-ignore
      _hmt.push(['_trackEvent', 'nav', 'click', `ghost=${data.index}`, data.index])
      event.preventDefault()
      if (this.selectGhosts.length >= 5) {
          this.$parent.$parent.showTips('最多只能选择5个哦')
          return
      }
      data.select = true
      setTimeout(() => {
          data.select = false
      }, 1000)
      this.selectGhosts.push(data)
  }

  private mounted () {
      if ((window.innerHeight / window.innerWidth) > 1.8) {
          this.changeBG = true
      }
      this.addBackGhosts()
      this.change()
  }
}
